import React from "react";
import MyEditor from "./editor";
import {Route,Switch,Link} from "react-router-dom"
import {
    AppBar,
    CssBaseline,
    Toolbar,
    Typography,
    Grid,
    Tab,
    Tabs,
    Button
} from "@material-ui/core";
import Web from "./web";
import C_Cpp from "./c_cpp";

class MainPage extends React.Component {
    constructor(props)
    {
        super(props);
        this.state={
            value:3,
            html:`<!--Write your html here-->`,
            css:"/*Write css here*/",
            js:"//Write javascript here",
            cpp:"//Write your c++ code here",
            input:""
        }
    }
    
   
    render() {
        const {socket}=this.props;

        return (
            <CssBaseline>
                <AppBar position="static" color="primary">
                    <Toolbar>
                        <Typography variant="h6" color="inherit"  style={{flex: 1,}} align="left">
                            Code Editor
          </Typography>
          <Link to="/web">
          <Button
        //   style={{
            //       flex:1,
            //       flexGrow: 1,
            //   }}
            align="right"
            >
              web
          </Button>
            </Link>
            <Link to="/c_cpp" >
          <Button>
              c_cpp
          </Button>
          </Link> 
                    </Toolbar>
                </AppBar>
                <Switch>
                <Route path="/c_cpp" component={()=><C_Cpp socket={socket}></C_Cpp>}/>
                <Route path="/web" component={()=><Web></Web>}/>
                <Route path="/" component={()=><Web></Web>}/>
                </Switch>
                {/* <Web></Web> */}
            </CssBaseline>
        );
    }
}
export default MainPage;